#navbar .nav-wrapper ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
    gap: 2rem;
    font-weight: 400;
    font-size: 1.2rem;
}

#navbar .nav-wrapper ul .right{
    margin-left: auto;
}

#navbar .nav-wrapper ul li a{
    display: block;
    color: #000;
    text-decoration: none;
    /* font-family: "RosesBolero", 'Playfair Display', 'Times New Roman', serif; */
}

