    background-color: #0000001e;
    border: 1px solid #000000;
    color: #000000;
    padding: 11px 26px;
    border-radius: 4px;
} */
#navbar .nav-wrapper ul li .brand{
    /* font-family: 'miracleregular', 'Playfair Display', 'Times New Roman', serif; */
    font-family: "RosesBolero", 'Playfair Display', 'Times New Roman', serif;
    letter-spacing: 2px;
    font-size: 1.1rem;
    font-weight: 600;
}
#navbar .nav-wrapper ul li .social-wrapper{
    display: flex;
    gap: 2rem;

}

#navbar .nav-wrapper ul li .social-wrapper .navlink{
    font-family: "RosesBolero", 'Playfair Display', 'Times New Roman', serif;
    letter-spacing: 2px;
    font-size: 0.9rem;
    font-weight: 600;
    cursor: pointer;
}

@media screen and (max-width: 720px) {
    #navbar .nav-wrapper ul li .social-wrapper{
        display: none;
    
    }
}